import { ComponentsService } from "./services";
export default class LTextView extends ComponentsService {
    constructor() {
        super(...arguments);
        this.serviceName = "LTextView";
    }
    render(h, formConfig, formMap) {
        let value = formMap[formConfig.textModel] || formConfig.defaultValue;
        return h("div", {}, value);
    }
}
